      <p><span>{{baseInfo.actualTotal}}</span>人</p>
    </div>
    <div class="item-box">
      <p class="title">履约总人数/年</p>
      <p><span>{{baseInfo.actualAppoint}}</span>人</p>
    </div>
    <div class="item-box">
      <p class="title">签约率</p>
      <p><span>{{baseInfo.actualTargetSignRate}}</span>%</p>
    </div>
    <div class="item-box">
      <p class="title">履约率</p>
      <p><span>{{baseInfo.appointRate}}</span>%</p>
    </div>
  </div>
</template>

<script>
export default {
  name: 'base-info',
  props: {
    baseInfo:{
      type:Object,
      default: ()=>{}
    }
  },
  watch: {
    baseInfo: {
　　　handler: function (newVal,oldval) {
        this.baseInfo = newVal;
　　　},
　　　deep: true
　　}
  }
}
</script>

