<!-- 折线图 -->
<template>
  <div :id="id" class="charts"></div>
</template>

<script>
import $echarts from 'echarts'
export default {
  name: 'line-chart',
  props: {
    chartData:{
      type:Object,
      default: ()=>{}
    },
    id:{
      type:String,
      default:'charts'
    }
  },
  watch: {
    chartData: {
　　　handler: function (newVal,oldval) {
        this.chartData = newVal;
        this.init();
　　　},
　　　deep: true
　　}
  },
  methods:{
    init(){
      const line = $echarts.init(document.getElementById(this.id));
      line.setOption({
          color:["#0be1e5","#fdba47"],
          tooltip: {
            trigger: 'axis',
            formatter:function(data){
              let result=''+data[0].name+'<br/>';
              for(let i=0;i<data.length;i++){
                 result+=data[i].marker+data[i].seriesName+"："+data[i].value+'人'+'<br/>';
              } 
              return result;
            }
          },
          title: {
            text:this.chartData.currYear+'年月度签约履约情况',
            left:'3%',
            textStyle:{
              color:'#62523a',
              fontSize:16
            }
          },
          grid: {
            left: '3%',
            right: '3%',
            bottom:'0%',
            containLabel: true
          },
          textStyle:{
            fontSize:14
          },
          legend: {
            itemGap:60,
            data:['签约人数','履约人数']      
          },
          xAxis : [
            {
              type : 'category',
              boundaryGap: false,
              data:this.chartData.xAxis||[],
              axisLine:{
                lineStyle:{
                  color:'#f5efea'
                }
              },
              axisLabel: {
                color:"#746651",
                textStyle: {
                   fontSize : 14  //更改坐标轴文字大小
                }
              }
            }
          ],
          yAxis : [
            {
              type : 'value',
              min:0,
              max:null,
              splitNumber:5,
              axisLine:{
                lineStyle:{
                  color:'#f5efea'
                }
              },
              splitLine:{
                lineStyle:{
                  color:'#f5efea',
                  type:'dashed'
                }
              },
              axisLabel: {
                color:"#746651",
                textStyle: {
                  fontSize : 14  //更改坐标轴文字大小
                }
              }
            }
          ],
          series:[
           {
              name:'签约人数',
              type:'line',
              smooth: true,
              symbolSize:10,
              lineStyle: {
                normal: {
                    color:'#0be1e5',
                    width: 4,
                    shadowColor: 'rgba(169, 207, 255, 0.8)',
                    shadowBlur: 8,
                    shadowOffsetY: 6
                }
             },
             areaStyle:{
               color: {
                  type: 'linear',
                  x: 0,
                  y: 0,
                  x2: 0,
                  y2: 1,
                  colorStops: [{
                      offset: 0, color: '#77e1eb' // 0% 处的颜色
                  }, {
                      offset: 1, color: '#4fb2f1' // 100% 处的颜色
                  }],
                  global: false // 缺省为 false
              },
              opacity:0.2,
              origin:'satrt',
             },
             data:this.chartData.actualTotal||[]
            },
            {
              name:'履约人数',
              type:'line',
              smooth: true,
              symbolSize:10,
              lineStyle: {
                normal: {
                    color:'#fdba47',
                    width: 4,
                    shadowColor: 'rgba(255,220,169,0.8)',
                    shadowBlur: 8,
                    shadowOffsetY: 5
                }
              },
              areaStyle:{
                color: {
                    type: 'linear',
                    x: 0,
                    y: 0,
                    x2: 0,
                    y2: 1,
                    colorStops: [{
                        offset: 0, color: '#ffb884' // 0% 处的颜色
                    }, {
                        offset: 1, color: '#ffc387' // 100% 处的颜色
                    }],
                    global: false // 缺省为 false
                },
                opacity:0.2,
                origin:'start',
              },
              data:this.chartData.actualAppoint||[]
          }
        ]
      })
      window.addEventListener('resize', function () {
          line.resize();
      });
    }
  }
}
</script>

<style lang="less" scoped>
  .charts{
      width:100%;
      height:500px;
  }
</style>
