<template>
  <!-- 首页 -->
  <div class="statistics-box">
    <!-- 基本信息统计 -->
    <BaseInfo :baseInfo="baseInfo"></BaseInfo>
    <!-- 图表 -->
    <div class="echart-box">
      <LineChart :id="'totalChart'" :chartData="lineChartData" v-show="lineChartData"></LineChart>
    </div>
  </div>
</template>

<script>
import BaseInfo from "./base-info" //基本信息概况
import LineChart from "./line-chart" //折线图
export default {
  name: 'home-page',
  components: {
    BaseInfo,
    LineChart
  },
  data () {
    return {
       baseInfo:{}, //基本信息
       lineChartData:{
         currYear:'',
         xAxis:[],
         actualAppoint:[],
         actualTotal:[]
       }
    }
  },
  created(){
    this.getBaseStatistics() //获取签约履约信息统计
  },
  methods: {
    //获取签约履约信息统计
    getBaseStatistics(){
      this.$http.get(process.env.VUE_APP_API+'/family/vin/getIndexStat').then(res => {
        if(res.code===200){
          let result = res.data
          this.baseInfo = result
          this.lineChartData = {}
          this.lineChartData.currYear = res.data.currYear
          this.lineChartData.xAxis = []
          this.lineChartData.actualAppoint = []
          this.lineChartData.actualTotal = []
          result.items.forEach(item=>{
            this.lineChartData.xAxis.push(item.month+'月') //月份
            this.lineChartData.actualAppoint.push(item.actualAppoint) //履约人数
            this.lineChartData.actualTotal.push(item.actualTotal) //签约人数
          })
        }else{
          this.$message.error(res.msg);
        }
      })
      .catch(error => {
        this.$message.error(error);
      });
    }
  }
}
</script>

<style lang='less' scoped>
.statistics-box{
  padding:45px 30px;
  .echart-box{
    margin-top:50px;
  }
}

</style>